.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

ul { list-style: none; display: flex; gap: 1em; cursor: default; }
li { }

.HSLView { width: 600px; height: 60px; color: white; }
.HSLControl .title { display: inline-block; width: 6ex; }
.HSLControl input[ type="range" ] { width: 300px }
.HSLControl .value { display: inline-block; width: 4ex; text-align: right; }
.HSLControl .unit { display: inline-block; width: 1em; }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
